import {
	mapState
} from 'vuex'

export default {
	data() {
		return {
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			// question:[
			// 	text:{
			// 		title:"问题1：黄河为什么是黄的？",
			// 		answer:["A:太多沙","B:太多沙","C:太多沙"],
			// 	},
			// ],
			text: {
				title: "问题1：黄河为什么是黄的？",
				answer: [{
						value: 'A',
						name: 'A:太多沙'
					},
					{
						value: 'B',
						name: 'B:太多沙',
						checked: 'true'
					},
					{
						value: 'C',
						name: 'C:太多沙'
					}
				]
			},
			current: 0,
		}
	},
	methods: {
		radioChange(evt) {
			for (let i = 0; i < this.text.answer.length; i++) {
				if (this.text.answer[i].value === evt.target.value) {
					this.current = i;
					break;
				}
			}
		},
		upper: function(e) {
			console.log(e)
		},
		lower: function(e) {
			console.log(e)
		},
		scroll: function(e) {
			console.log(e)
			this.old.scrollTop = e.detail.scrollTop
		},
		goTop: function(e) {
			// 解决view层不同步的问题
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
			uni.showToast({
				icon: "none",
				title: "纵向滚动 scrollTop 值已被修改为 0"
			})
		},
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
	onLoad() {
		if (!this.hasLogin) {
			uni.showModal({
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				/**
				 * 如果需要强制登录，不显示取消按钮
				 */
				showCancel: !this.forcedLogin,
				success: (res) => {
					if (res.confirm) {
						/**
						 * 如果需要强制登录，使用reLaunch方式
						 */
						if (this.forcedLogin) {
							uni.reLaunch({
								url: '../login/login'
							});
						} else {
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				}
			});
		}
	}
}
